<template>
  <div>
    <mrc_ess_table :headers='headers'
                   v-if="$route.name == 'data-access-strategy'"
                   pageName='data-access-strategy-add'
                   :paginationOptions='paginationOptions'
                   @turnPage='getList'
                   :isLoading='loading'
                   :slotKey="['strategy_number']"
                   :items='desserts'>

      <template v-slot:item.strategy_number='{item}'>
        <span v-for="(item1,index) in item.strategy_number.split(',')"
              :key='index'>
          <v-chip v-if="item1"
                  class="mr-2">{{item1}}</v-chip>
        </span>
      </template>

      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import { listData, delData } from '@/service/api/data-access-strategy'
export default {
  components: {},
  props: {},
  data () {
    return {
      desserts: [],
      loading: false,
      paginationOptions: {
        pagesize: this.$store.state.pageSize,
        cpage: this.$store.state.cpage,
        totalCount: 0, //总条数
        totalpage: 0, //总页数
      },
    };
  },
  validations: {},
  computed: {
    menulist () {
      return [
        {
          name: this.$t('label_View'),
          show: true
        },
        {
          name: this.$t('label_Edit'),
          show: true
        },
        {
          name: this.$t('label_delete'),
          show: true
        }
      ]
    },
    headers () {
      return [
        {
          text: this.$t('label_Stored_procedure_name'),
          align: 'start',
          sortable: false,
          value: 'org_level_name',
        },
        {
          text: '',
          align: 'start',
          sortable: false,
          value: 'strategy_number',
        },
        {
          text: ' ',
          align: 'start',
          sortable: false,
          value: 'actions',
          width: '20'
        }
      ]
    }
  },
  created () { },
  watch: {
    async '$route' (val) {
      if (val.name == 'data-access-strategy') {
        await this.getList(this.paginationOptions)
      }
    }
  },
  async mounted () {
    await this.getList()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    async getList (obj) {
      if (obj) {
        this.paginationOptions = obj
      }
      this.loading = true
      this.desserts = []
      const res = await listData({
        cpage: this.paginationOptions.cpage,
        pagesize: this.paginationOptions.pagesize,
        org_level_uuid: this.$store.state.currentCompany
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.paginationOptions.totalCount = res.totalCount
        this.paginationOptions.totalpage = res.totalpage
      }
      this.loading = false
    },
    handleClick ({ index }, item) {
      if (index == 0) {
        this.$store.commit('set_subPage_name', item.org_level_name)
        this.$store.commit('set_uuid', item.org_level_uuid)
        this.$store.commit('set_iconstatus', true)  //行内进图标显示
        this.$router.push({
          name: 'data-access-strategy-detail',
          params: {
            name: item.org_level_uuid,
            uuid: item.uuid
          }
        })
      }
      // 编辑
      if (index == 1) {
        this.$store.commit('set_subPage_name', item.org_level_name)
        this.$store.commit('set_uuid', item.org_level_uuid)
        this.$store.commit('set_iconstatus', false)
        this.$router.push({
          name: 'data-access-strategy-detail',
          params: {
            name: item.org_level_uuid,
            uuid: item.uuid
          }
        })
      }
      if (index == 2) {
        this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') })
          .then(async () => {
            const del = await delData({
              org_level_uuid: item.org_level_uuid
            })
            if (del.code == 200) {
              // this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
              this.$Message({ text: this.$t('message_The_deletion_was_successful') })
              // this.desserts.length === 1  只有一条数据 删除就请求上一页的数据
              if (this.desserts.length === 1 && this.paginationOptions.cpage > 1) {
                this.paginationOptions.cpage--
              }
              await this.getList()
            }
          }).catch(() => { })
      }
    },
  },
};
</script>

<style scoped>
.procedure_name {
  display: flex;
  flex-direction: column;
  padding: 10px 0;
}
.name {
  font-size: 16px;
}
.desc {
  color: #aaaaaa;
}
</style>
